import React from 'react';
import { PanelHeaderButton, IOS, platform, Card, CardGrid, Cell, List, Panel, PanelHeader, Group } from '@vkontakte/vkui';

//иконки
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; // Коронавирус
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline'; // О приложении
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline'; // Настройки
import Icon28TargetOutline from '@vkontakte/icons/dist/28/target_outline'; // Организации

//Иконки для ОС
import Icon28LogoVkOutline from '@vkontakte/icons/dist/28/logo_vk_outline';
import Icon24LogoVk from '@vkontakte/icons/dist/24/logo_vk';


const osname = platform();

const Home = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton>
        {osname === IOS ? <Icon28LogoVkOutline/> : <Icon24LogoVk/>}
    </PanelHeaderButton>}
        >
         Прайд
        </PanelHeader>
        <Group>
        <CardGrid>
<Card size="l" mode="shadow">
<List>
<Cell expandable before={<Icon28BugOutline />} onClick={props.go} data-to="covidmenu">Коронавирус</Cell>
<Cell expandable before={<Icon28ArticleOutline />} onClick={props.go} data-to="info">О приложении</Cell>
<Cell expandable before={<Icon28TargetOutline />} onClick={props.go} data-to="organizations">Организации города (#404)</Cell>
<Cell expandable before={<Icon28SettingsOutline />}>Настройки (#404)</Cell>
</List>
<div style={{ height: 20 }} />
</Card>
</CardGrid>
    </Group>
    </Panel>
);


export default Home;

