import React from 'react';
import { CardGrid, Card, Placeholder, platform, IOS, Panel, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';




//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //ios
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

import Icon56UsersOutline from '@vkontakte/icons/dist/56/users_outline';
import Icon28MoneyTransfer from '@vkontakte/icons/dist/28/money_transfer';
import Icon24MarketOutline from '@vkontakte/icons/dist/24/market_outline';





const osname = platform();

const Organizations = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            Организации
        </PanelHeader>


        <CardGrid>
          <Card size="l" mode="shadow">
          <Placeholder
            icon={<Icon28MoneyTransfer width={56} height={56} />}
            header="Сбербанк"
            
          >Советская ул., 166
          8 (800) 555-55-50
          08:30–17:30
            
          </Placeholder>
            <div style={{ height: 20 }} />
          </Card>
        </CardGrid>


        <CardGrid>
          <Card size="l" mode="shadow">
          <Placeholder
            icon={<Icon24MarketOutline width={56} height={56} />}
            header="Монетка"
            
          >ул. Энгельса, 9
          Номер неизвестен
          09:00–22:00
            
          </Placeholder>
            <div style={{ height: 20 }} />
          </Card>
        </CardGrid>


        <CardGrid>
          <Card size="l" mode="shadow">
          <Placeholder
            icon={<Icon24MarketOutline width={56} height={56} />}
            header="Пятёрочка"
            
          >ул. Энгельса, 12
          8 (800) 555-55-05
          08:00–22:00
            
          </Placeholder>
            <div style={{ height: 20 }} />
          </Card>
        </CardGrid>

      

        </Panel>
);


export default Organizations;